<template>
    <div>
      <header-component title="账号注册"></header-component>
      <router-view></router-view>
      <div class="am-top">
        <div class="am-form-box">
          <label class="zhanghao">账号</label>
          <div class="am-account">
            <input type="tel" class="txtSize"  placeholder="请输入您的账号" maxlength="13">
            <img class="am-account-cancel" src="../../assets/images/Login/Login_06.png"/>
          </div>

          <label class="mima">密码</label>
          <div class="am-passWord">
            <input type="password" class="am-pwd" placeholder="（6-16字母和数字组合）" maxlength="16">
            <img class="am-password-cancel" src="../../assets/images/Login/Login_05.png"/>
            <img class="am-password-show" src="../../assets/images/Login/Login_06.png"/>
          </div>
          <label class="mima">确认密码</label>
          <div class="am-passWord">
            <input type="password" class="am-pwd" placeholder="请再次输入密码" maxlength="16">
            <img class="am-password-cancel" src="../../assets/images/Login/Login_05.png"/>
            <img class="am-password-show" src="../../assets/images/Login/Login_06.png"/>
          </div>
          <p class="am-loginBtn">
            <button type="button" class="am-btn-default">注&nbsp;册</button>
          </p>
        </div>

      </div>
    </div>
</template>
<style lang="scss" scoped>
  @import "../../assets/css/visit/normalregister.scss";

</style>
<script>
  import HeaderComponent from './header.vue'

    export default {
    name:"normalregister",
        data() {
            return {
            }
        },
        components: {
          HeaderComponent,
        }
    }
</script>



